const Tool = {//

    //TODO *************************************************************************//

    setSession: (key, val) => sessionStorage.setItem(key, JSON.stringify(val)),//
    getSession: key => JSON.parse(sessionStorage.getItem(key)),//
    removeSession: key => sessionStorage.removeItem(key),  // session

    setCookie: (key, val) => document.cookie = key + '=' + val,//cookie
    getCookie: key => { //cookie
        let val = false;
        document.cookie.split("; ").map(item => {
            if (item.split("=")[0] === key)
                val = item.split("=")[1]
        });
        return val
    },
    removeCookie: key => {//cookie
        let exp = new Date();
        exp.setTime(exp.getTime() - 1);
        if (all.tool.getCookie(key) !== null) {
            all.tool.setCookie(key, all.tool.getCookie(key) + ";expires=" + exp.toGMTString())
        }
    },
    setLocal: (key, val) => localStorage.setItem(key, JSON.stringify(val)), // localStorage
    getLocal: key => JSON.parse(localStorage.getItem(key)),  // localStorage
    //---------------------------------------------------------------------------------------------//

    //TODO *************************************************************************//
    isType: type => Object.prototype.toString.call(type).slice(8, Object.prototype.toString.call(type).length - 1),//
    

};
export default Tool;


