export default {
    //  
    clickoutside: {
        bind(el, binding) {
            function documentHandler(e) {
                if (el.contains(e.target)) {
                    return false
                }
                if (binding.expression) {
                    binding.value(e)
                }
            }
            el.__vueClickOutside__ = documentHandler
            document.addEventListener('click', documentHandler)
        },
        unbind(el) {
            document.removeEventListener('click', el.__vueClickOutside__);

        }
    },
    /* 
        
        v-validate = Object
        {
            type: [String [phone|username|password] | RegExp],
            desc: String,			
            required: Boolean, 		
            err: String,			
        }
        */
    validate: {
        inserted(el, binding) {
            let tipDom = el.parentNode.lastElementChild,
                query = binding.value;
            el.addEventListener('keyup', () => {
                let val = el.value,
                    text;
                if (!val && query.required) {
                    text = query.desc ? `${query.desc || ''}` : ``
                } else {
                    switch (query.type) {
                        case 'phone':
                            text = /^1[34578]\d{9}$/.test(val) ? '' : (query.err || '')
                            break
                        case 'username':
                            text = /^(?![0-9]+$)(?![a-zA-Z]+$)[0-9A-Za-z]{4,16}$/.test(val) ? '' : (query.err || '4-16')
                            break
                        case 'password':
                            text = /^(?![0-9]+$)(?![a-zA-Z]+$)[0-9A-Za-z]{6,16}$/.test(val) ? '' : (query.err || '6-16')
                            break
                        case 'payPassword':
                            text = /[0-9a-zA-Z]{6,12}/.test(val) ? '' : (query.err || '6-12')
                            break
                        case 'email':
                            text = /^\w+((.\w+)|(-\w+))@[A-Za-z0-9]+((.|-)[A-Za-z0-9]+).[A-Za-z0-9]+$/.test(val) ? '' : (query.err || '')
                            break
                        default:
                            if (query.type instanceof RegExp) {
                                if (query.type.test(val)) {
                                    text = ''
                                } else {
                                    if (query.err) {
                                        text = query.err
                                    } else {
                                        throw new Error('Directive: validate, binding value syntax error!')
                                    }
                                }
                            }
                    }
                }
                tipDom.className = text ? 'err-msg' : ''
                tipDom.innerText = text ? text : ''
            })
        }
    },
    /* 
        
        v-checkSubmit = Object
        {   
            id: formid,
            fn: 
        }
        */
    checkSubmit: {
        inserted(el, binding) {
            el.addEventListener('update', e => {
                e.preventDefault()
                let form = document.querySelector('#' + binding.value.el),
                    inputs = form.querySelectorAll('input'),
                    eventObj = new Event('keyup', {
                        bubbles: true,
                        cancelable: true
                    });
                inputs.forEach(item => {
                    item.dispatchEvent(eventObj)
                })
                if (form.querySelectorAll('.err-msg').length === 0) {
                    binding.value.fn && binding.value.fn()
                }
            })
        }
    },

    /* 
        input
        v-inputLimit = String [number|p-number|integer|p-integer|no-zh-cn]
    */
    inputLimit: {
        inserted(el, binding) {
            let reg
            switch (binding.value) {
                case 'number':
                    reg = /[^\-?\d.]/g // 
                    break
                case 'p-number': // 
                    reg = /[^\d.]/g
                    break
                case 'integer':
                    reg = /[^\-?\d]/g // 
                    break
                case 'p-integer': // 
                    reg = /\D/g
                    break
                case 'no-zh-cn':  // 
                    reg = /[\u4E00-\u9FA5]*/g
                    break
                default:
            }
            el.addEventListener('keyup', () => {
                el.value = el.value.replace(reg, '')
            })
            el.addEventListener('onafterpaste', () => {
                el.value = el.value.replace(reg, '')
            })
        }
    }

}


