<template>
    <div class="container">
        <div class="filter">
            <div class="left">
                <div>
                    <span></span>
                    <Input style="width:110px" class="game-account" />
                </div>
                <div>
                    <span>ID</span>
                    <Input style="width:110px" class="game-id"  />
                </div>
                <div>
                    <span></span>
                    <Date />
                </div>
            </div>
            <div class="right">
                <span>
                    <button class="btn-blue"></button>
                </span>
                <span>
                    <button class="btn-blue">Excel</button>
                </span>
            </div>
        </div>
        <div class="table">
            <Table :headers="headers" :column="list">
                <template v-slot:item="{row}">
                    <td style="height:30px">{{row.b}}</td>
                    <td>{{row.a}}</td>
                    <td>{{row.b}}</td>
                    <td>{{row.b}}</td>
                    <td>{{row.c}}</td>
                    <td>{{row.d}}</td>
                </template>
            </Table>
            <Page 
                class="page"
                :total="total"
                :pagerCount="pagerCount"
                @updateNo="updateNo"
                @updateSize="updateSize"
            />
        </div>
    </div>
</template>

<script>
export default {
    data() {
        return {
            game_plant: "2",
            game_plant_option: [
                { label: "", value: "2" },
                { label: "", value: "3" }
            ],
            user_id: "",
            headers: [
                { label: "" },
                { label: "ID" },
                { label: "IP" },
                { label: "" },
                { label: "" },
                { label: "" }
            ],
            list: [
                {
                    a: "1",
                    b: "2",
                    c: "192.168.1.1(.",
                    d: "2019/12/15 12:12:00"
                },
                {
                    a: "1",
                    b: "2",
                    c: "192.168.1.1(.",
                    d: "2019/12/15 12:12:00"
                }
            ],
            total: 0,
            pagerCount: 0
        };
    },
    methods: {
      
        updateNo(val) {},
        updateSize(val) {}
    },
    mounted() {}
};
</script>

<style scoped>
.container {
    padding: 10px 8px;
    background: #fff;
    border-radius: 5px;
    border-top-left-radius: 0;
}
.filter {
    /* display: flex; */
    line-height: 30px;
}
/* .filter > ul > li {
    margin-right: 15px;
    line-height: 28px;
}
.filter {
    display: flex;
    justify-content: space-between;
    padding: 0 10px;
} */
.table {
    margin-top: 15px;
}
.page {
    margin-top: 30px;
    display: flex;
    justify-content: center;
}
</style>


