import Vue from 'vue'
import Vuex from 'vuex';


Vue.use(Vuex);

const state = {
    isShowLoading: false,
    tab_nav_list:[]         // 
};
const mutations = {};
for (let item in state) {
    mutations['update'+item] = (state, value) => {
        state[item] = value
    }
}
export default new Vuex.Store({
    state,
    mutations
})


