<template>
    <div class="container">
        <div class="filter">
            <div class="left">
                <div>
                    <span></span>
                    <Input class="game-account" />
                </div>
                <div>
                    <span>ID</span>
                    <Input class="game-id" />
                </div>
                <div>
                    <span></span>
                    <Date />
                    <span></span>
                    <Date />
                </div>
                <div class="acc-status">
                    <span></span>
                    <!-- <Input class="account-status" size="small"/> -->
                    <span>
                        <Select
                            v-model="online_state"
                            :options="online_state_opt"
                            style="width:70px;"
                            @update="selectupdate"
                        ></Select>
                    </span>
                </div>
                <div class="acc-status">
                    <span>IP</span>
                    <!-- <Input class="account-status" size="small"/> -->
                    <span>
                        <Date />
                    </span>
                </div>
            </div>
            <div class="right">
                <span>
                    <button class="btn-blue"></button>
                </span>
                <span>
                    <button class="btn-blue">Excel</button>
                </span>
            </div>
        </div>
        <div class="tool-bar">
            <button class="btn-plain" @click="addAccClick"></button>
            <!-- <button class="btn-plain"></button>
            <button class="btn-plain"></button>
            <button class="btn-plain"></button>
            <button class="btn-plain"></button>
            <button class="btn-plain"></button>
            <button class="btn-plain"></button>
            <button class="btn-plain"></button>
            <button class="btn-plain"></button>
            <button class="btn-plain"></button>-->
        </div>

        <Table
            :headers="headers"
            :column="list"
            @checkboxChange="checkboxChange"
            :haveCheckbox="false"
        >
            <template v-slot:item="{row}">
                <td class="table-td">{{row.a}}</td>
                <td>{{row.b}}</td>
                <td>{{row.c}}</td>
                <td>{{row.d}}</td>
                <td>{{row.d}}</td>
                <td>{{row.d}}</td>
                <td>
                    <div>{{row.d}}</div>
                    <div>{{row.d}}</div>
                </td>
                <td>
                    <div>{{row.d}}</div>
                    <div>{{row.d}}</div>
                </td>
                <td>{{row.d}}</td>
                <td>
                    <div class="table-opra">
                        <span @click="showDetail"></span>
                        <span @click="addBlackList"></span>
                    </div>
                </td>
            </template>
        </Table>
        <div class="page">
            <Page
                :total="total"
                :pagerCount="pagerCount"
                @updateNo="updateNo"
                @updateSize="updateSize"
            />
        </div>
        <div class="modal-mask" v-if="show_detail">
            <div class="v-modal">
                <!-- // mask  -------------- -->
                <div class="detail">
                    <div class="close-icon" @click="closeUserList()">
                        <i class="iconfont iconcuowuguanbi-"></i>
                    </div>
                    <!-- - -->
                    <div class="row1 pic">
                        <div>
                            <img
                                class="avator"
                                src="../../../assets/image/user/neko.png"
                                alt=""
                            />
                        </div>
                        <div class="user-info">
                            <tr>
                                <td class="head-2"></td>
                                <td>VIP:VIP1</td>
                                <td></td>
                            </tr>
                            <tr>
                                <td style="height:46px;">ID:66666</td>
                                <td>: 0.00</td>
                                <td v-if="EditUserTab" class="flex">
                                    <Select v-model="user_tab" :options="user_tab_opt"></Select>
                                    <span class="a"></span>
                                    <span class="a" @click="EditUserTab=false"></span>
                                </td>
                                <td v-if="!EditUserTab" class="flex">
                                    <span class="bg-red">
                                        <i class="iconfont iconjinggao1-"></i>
                                        <span>{{''}}</span>
                                    </span>
                                    <span class="a" @click="EditUserTab=true"></span>
                                </td>
                            </tr>
                        </div>
                    </div>
                    <!--  -->
                    <div class="row2 info">
                        <div class="head-2"></div>
                        <div class="list">
                            <ul>
                                <li>
                                    <span>:</span>
                                    <span class="a"></span>
                                </li>
                                <li>
                                    <span>:</span>
                                    <span class="a"></span>
                                </li>
                                <li>
                                    <span>:</span>
                                    <span class="a"></span>
                                </li>
                                <li>
                                    <span>:</span>
                                    <span class="a"></span>
                                </li>
                                <li>
                                    <span>:</span>
                                    <span class="a"></span>
                                </li>
                            </ul>
                        </div>
                    </div>
                    <!--  -->
                    <div class="row3 promotion">
                        <div class="head-2"></div>
                        <div class="list">
                            <ul>
                                <li>
                                    <span>:</span>
                                    <span>{{'----'}}</span>
                                </li>
                                <li>
                                    <span>:</span>
                                    <span>{{'100'}}</span>
                                </li>
                                <li>
                                    <span>:</span>
                                    <span>{{'0.00'}}</span>
                                </li>
                            </ul>
                        </div>
                    </div>
                    <!--  -->
                    <div class="row4 money">
                        <div class="head-2"></div>
                        <div class="list">
                            <ul>
                                <li>
                                    <span>:</span>
                                    <span>{{'0.00'}}</span>
                                </li>
                                <li>
                                    <span>:</span>
                                    <span>{{'100'}}</span>
                                </li>
                                <li>
                                    <span>:</span>
                                    <span>{{'0.00'}}</span>
                                </li>
                                <li>
                                    <span>:</span>
                                    <span>{{'0.00'}}</span>
                                </li>
                            </ul>
                        </div>
                        <div class="list">
                            <ul>
                                <li>
                                    <span>:</span>
                                    <span>{{'0.00'}}</span>
                                </li>
                                <li>
                                    <span>:</span>
                                    <span>{{'100'}}</span>
                                </li>
                            </ul>
                        </div>
                        <div class="list">
                            <ul>
                                <li>
                                    <span>:</span>
                                    <span>{{'0.00'}}</span>
                                </li>
                                <li>
                                    <span>:</span>
                                    <span>{{'100'}}</span>
                                </li>
                            </ul>
                        </div>
                        <div class="list">
                            <ul>
                                <li>
                                    <span>:</span>
                                    <span>{{'0.00'}}</span>
                                </li>
                            </ul>
                        </div>
                    </div>
                    <!--  -->
                    <div class="row5 other">
                        <div class="head-2"></div>
                        <div class="list">
                            <ul>
                                <li>
                                    <span>:</span>
                                    <span>{{'IOS'}}</span>
                                </li>
                                <li>
                                    <span>IP:</span>
                                    <span>{{'IOS'}}</span>
                                </li>
                                <li>
                                    <span>:</span>
                                    <span>{{'IOS'}}</span>
                                </li>
                            </ul>
                        </div>
                        <div class="list">
                            <ul>
                                <li>
                                    <span>:</span>
                                    <span>{{'IOS'}}</span>
                                </li>
                                <li>
                                    <span>:</span>
                                    <span>{{'IOS'}}</span>
                                </li>
                                <li>
                                    <span>IP:</span>
                                    <span>{{'IOS'}}</span>
                                </li>
                            </ul>
                        </div>
                        <div class="list">
                            <ul>
                                <li>
                                    <span>:</span>
                                    <span>{{'2019/05/16 15:15:00'}}</span>
                                </li>
                                <li>
                                    <span>:</span>
                                    <span>{{'IOS'}}</span>
                                </li>
                                <li>
                                    <span>:</span>
                                    <span>{{'IOS'}}</span>
                                </li>
                            </ul>
                        </div>
                    </div>
                </div>
            </div>
        </div>
        <div v-if="inner_mask_show" class="inner-mask">
            <div class="confirm v-model">
                <div class="close-icon" @click="closeConfirm()">
                    <i class="iconfont iconcuowuguanbi-"></i>
                </div>
                <div class="form">
                    <ul>
                        <li>
                            <span>:</span>
                            <Input v-model="add_acc" />
                        </li>
                        <li>
                            <span>:</span>
                            <Input v-model="add_acc" type="password" />
                        </li>
                        <li>
                            <span>:</span>
                            <Input v-model="add_acc" type="password" />
                        </li>
                    </ul>
                    <div class="confirm-btn">
                        <button class="btn-blue-normal"></button>
                    </div>
                </div>
            </div>
        </div>
        <div class="modal-mask" v-if="show_add_black_list">
            <div class="blacklist">
                <div class="blacklist-inner">
                    <div class="infor" ></div>
                    <div class="btn">
                        <button class="btn-plain-normal" @click="show_add_black_list=false"></button>
                        <button class="btn-blue-normal"></button>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
export default {
    data() {
        return {
            headers: [
                { label: "" },
                { label: "" },
                { label: "ID" },
                { label: "" },
                { label: "" },
                { label: "" },
                { label: "" },
                { label: "IP,IP" },
                { label: ", " },
                { label: "" }
            ],
            list: [
                { a: "", b: "132****4654", c: "3", d: "4" },
                { a: "", b: "132****4654", c: "3", d: "4" }
            ],
            online_state: "",
            online_state_opt: [
                { label: "", value: "" },
                { label: "", value: "1" },
                { label: "", value: "2" }
            ],
            total: 0,
            pagerCount: 0,
            show_detail: false,
            use_detail: false,
            add_acc: "",
            user_tab: "",
            EditUserTab: false,
            user_tab_opt: [
                { label: "", value: 1 },
                { label: "", value: 2 }
            ],

            inner_mask_show: false,
            show_add_black_list: false
        };
    },
    methods: {
        checkboxChange(whitch, is_checked, check_list) {
            console.log(" check_list", check_list);
            console.log("whitch", whitch);
            console.log("", is_checked);
            console.log(this.list);
        },
        selectupdate(val) {
            this.$toast.info("");
        },

        showDetail() {
            this.show_detail = true;
        },
        addBlackList() {
            this.show_add_black_list = true;
        },
        closeUserList() {
            this.show_detail = false;
        },
        addAccClick() {
            this.inner_mask_show = true;
        },
        closeConfirm() {
            this.inner_mask_show = false;
        },
        updateNo(val) {},
        updateSize(val) {}
    },
    mounted() {}
};
</script>

<style scoped>
.container {
    font-size: 14px;
    background: #fff;
    padding: 10px 8px;
    border-radius: 5px;
    border-top-left-radius: 0;
}
.filter {
    line-height: 30px;
}
.game-account,
.game-id,
.ip-address {
    width: 100px;
}
/*  */
.game-date {
    width: 80px;
}
.account-status {
    width: 80px;
}
.acc-status {
    /* display: flex;
        align-items: center; */
    /* display: inline-block; */
}

.btn-blue {
    /* padding: 4px 15px; */
    outline: none;
    border: none;
    border-radius: 3px;
    color: #fff;
    background: #4c8bfd;
    box-shadow: 1px 1px 2px rgba(0, 0, 0, 0.2);
    cursor: pointer;
}
.btn-blue:hover {
    background: #6a9bf5;
    box-shadow: none;
}
.filter .btn-blue {
    padding: 4px 15px;
}
.tool-bar .btn-blue {
    padding: 6px 15px;
}
.tool-bar {
    margin: 30px 0;
}
.table-td {
    /* width: 300px; */
    text-align: center;
    padding: 18px 6px;
}
/* .modal-mask   css*/
.v-modal {
    min-width: 500px;
    min-height: 500px;
    position: absolute;
    top: 100px;
    z-index: 2;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    background-color: #fff;
    border-radius: 7px;
}

/*  */
.inner-mask .confirm {
    min-width: 450px;
    min-height: 350px;
    position: absolute;
    top: 100px;
    z-index: 3;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    padding: 10px;
    background-color: #fff;
    border-radius: 7px;
}
.confirm .form {
    width: 300px;
    position: relative;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
}
.confirm .form ul li {
    margin-top: 30px;
}
.confirm .form ul li {
    display: flex;
    align-items: center;
}
.confirm .form ul li > span:first-child {
    margin-right: 10px;
}
.blacklist {
    min-width: 450px;
    min-height: 260px;
    position: absolute;
    top: 50%;
    z-index: 2;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    background-color: #fff;
    border-radius: 7px;
}
.blacklist-inner {
    position: absolute;
    top: 50%;
    z-index: 2;
    left: 50%;
    top: 40%;
    transform: translateX(-50%);
    white-space: nowrap;
    text-align: center;
    font-size: 20px;
}
.blacklist-inner .btn button{
    margin-top: 70px;
    /* padding: 8px 16px; */
}
.blacklist-inner .btn .btn-plain-normal{
    margin-right: 60px;
}
.table-opra span {
    font-size: 14px;
    font-weight: border;
    text-decoration: underline;
    cursor: pointer;
    color: #4c8bfd;
}
.a {
    font-weight: border;
    text-decoration: underline;
    cursor: pointer;
    color: #4c8bfd;
    margin-left: 8px;
}
.a:active {
    color: red;
}
.page {
    margin-top: 30px;
    display: flex;
    justify-content: center;
}
.detail {
    width: 800px;
    position: relative;
    padding: 25px;
}
.confirm .iconcuowuguanbi-,
.modal-mask .iconcuowuguanbi- {
    position: absolute;
    right: 12px;
    top: 12px;
    font-size: 35px;
    color: #4c8bfd;
}
.confirm .iconcuowuguanbi-:hover,
.modal-mask .iconcuowuguanbi-:hover {
    color: #749ff0;
    cursor: pointer;
}
.v-modal .detail .row1 {
    display: flex;
}
.detail .user-info {
    /* margin-top: 20px; */
}
.detail .user-info tr td {
    padding: 6px 12px;
}
.avator {
    width: 85px;
    height: 85px;
    overflow: hidden;
    border: 3px solid #4c8bfd;
    border-radius: 5px;
}
.head-2 {
    font-size: 16px;
    font-weight: 600;
    color: #4c8bfd;
}
.modal-mask .row1,
.modal-mask .row2,
.modal-mask .row3,
.modal-mask .row4,
.modal-mask .row5 {
    margin-top: 24px;
}
.modal-mask .list ul {
    display: flex;
}
.list {
    margin-top: 10px;
}
.list ul li {
    font-weight: 600;
    color: rgb(83, 83, 83);
}
.list ul li:not(:last-child) {
    margin-right: 45px;
}
.bg-red {
    padding: 4px 6px;
    border-radius: 5px;
    background: #fe4c4c;
    color: #fff;
}
.confirm-btn {
    margin-top: 30px;
    text-align: center;
}
.confirm-btn .btn-blue {
    padding: 8px 14px;
}
</style>


