import Vue from 'vue'
import Router from 'vue-router'

import HomePage from '@/components/views/home/homePage.vue'
import Login from '@/components/Login.vue'
// 
import Test from '@/components/Test.vue'
/*  */
  // 
import UserList from '@/components/views/user/UserList.vue'
import LoginLog from '@/components/views/user/LoginLog.vue'
import TabManage from '@/components/views/user/TabManage.vue'

Vue.use(Router)

export default new Router({
  mode: 'history',
  routes: [
    {
      path: '/',
      component: HomePage
    },
    {
      path: '/test',
      component: Test
    },
    {
      path: '/login',
      component: Login
    },

    {
      path: '/home',
      component: HomePage
    },

   /*   */

    // 
    {
      path: '/user/userlist',
      component: UserList
    },
    // 
    {
      path: '/user/loginlog',
      component: LoginLog
    },
    // 
    {
      path: '/user/tabmanage',
      component: TabManage
    },
   /*   */
  ]
})



